export default {
	data() {
		return {
			placeholder: '搜索你想要的商品',
			height: '',
			history_list: [],
			hotList: [], //热门搜索
			guessList: [], //猜你喜欢
			keyword: '', //关键词
			searchTrue: false, //是否搜索
			marketid: '',
			prodList: [], //全部商品
			prod_show: false,
			token: '',
			isEmpty: '',
			myIndex: ''

		}
	},
	onLoad: function() {
		this.marketid = uni.getStorageSync('marketid')
		this.history_list = uni.getStorageSync('history_list')
		this.token = uni.getStorageSync('token')
		if (this.history_list == '') {
			this.history_list = []
		}
	},
	onShow: function() {
		this.getHeight()
		this.getHotsearch()
	},
	methods: {
		//计算高度
		getHeight: function() {
			let info = uni.createSelectorQuery().select(".search_box");
			info.boundingClientRect((data) => { //data - 各种参数
				this.height = ((getApp().globalData.screenHeight - getApp().globalData.statusBarHeight - getApp().globalData.titleBarHeight) -
					data.height) + 'px'
			}).exec()
		},

		//获取热门搜索
		getHotsearch: function() {
			this.$urlRequest('getHotProduct.do', {
				marketid: this.marketid
			}).then(res => {
				this.hotList = res.data
			})
		},
		//搜索
		submit: function() {
			if (this.keyword == "") {
				this.$myToast('请输入商家名称或者菜品名称')
				return;
			}
			this.searchTrue = true
			this.$urlRequest('getProductByName.do', {
				marketid: this.marketid,
				name: this.keyword,
				type:this.myIndex
			}).then(res => {
				if (res.data.length != 0) {
					for (var item of res.data) {
						for (var key in item) {
							if (item[key] == null || item[key] == 'null') {
								item[key] = ''
							}
							if (key == 'prodimg') {
								item[key] = JSON.parse(item[key])
							}
						}
					}
					this.prodList = res.data
					this.isEmpty = false
				} else {
					this.prodList = []
					this.isEmpty = true
					this.guessLove()
				}
			})
			let history_list = this.history_list;
			let flag = false;
			for (var i = 0; i < this.history_list.length; i++) {
				if (this.history_list[i] == this.keyword) {
					flag = true;
				}
			}
			if (!flag) {
				if (this.history_list.length == 10) {
					history_list.pop();
				}

				history_list.unshift(this.keyword)
			} else {
				for (var i = 0; i < this.history_list.length; i++) {
					if (this.history_list[i] == this.keyword) {
						this.history_list.splice(i, 1)
					}
				}
				history_list.unshift(this.keyword)
			}
			uni.setStorage({
				key: "history_list",
				data: history_list
			})
			this.prod_show = true
			this.history_list = history_list

		},
		//获取猜你喜欢
		guessLove: function() {
			this.$urlRequest('getLikeProd.do', {
				marketid: this.marketid,
			}).then(res => {
				for (var item of res.data) {
					for (var key in item) {

						if (item[key] == null || item[key] == 'null') {
							item[key] = ''
						}
						if (key == 'prodimg') {
							item[key] = JSON.parse(item[key])
						}
					}
				}
				this.guessList = res.data
			})
		},
		//选择
		chooseMe: function(e) {
			this.keyword = e.currentTarget.dataset.title
			this.submit()
		},
		getVal: function() {
			this.prod_show = false;
			this.myIndex = ''
			if (this.keyword == '') {
				this.searchTrue = false
			}
		},
		//加入购物车
		add_shoppingcar: function(prodId) {
			console.log(prodId)
			if (!!!this.token) {
				uni.getSetting({
					success(res) {
						uni.navigateTo({
							url: '/pages/login/login_type',
						})
					}
				})
			} else {
				var data = {
					token: this.token,
					prodid: prodId
				}
				uni.showLoading({
					title:'正在加入购物车'
				})
				this.$urlRequest('addBusinessCarProd.do', data).then(res => {
					uni.hideLoading()
					if (res.state == 'true') {
						this.$myToast('已加入购物车')
					} else {
						this.$myToast(res.state)
					}
				})
			}
		},
		//清除历史搜素
		clearHistory: function() {
			uni.showModal({
				content: '确定清除所以历史记录?',
				success: (res) => {
					if (res.confirm) {
						this.history_list = []
						uni.setStorage({
							key: "history_list",
							data: this.history_list
						})
						this.$myToast('已全部清除')
					}
				}
			});
		},
		gotoDetail: function(prodId) {
			uni.navigateTo({
				url: '/pages/prodDetail/prodDetail?prodId=' + prodId
			})
		},
		//
		changeNav: function(type) {
			this.myIndex = type;
			this.$urlRequest('getProductByName.do', {
				marketid: this.marketid,
				name: this.keyword,
				type:this.myIndex
			}).then(res => {
				if (res.data.length != 0) {
					for (var item of res.data) {
						for (var key in item) {
							if (item[key] == null || item[key] == 'null') {
								item[key] = ''
							}
							if (key == 'prodimg') {
								item[key] = JSON.parse(item[key])
							}
						}
					}
					this.prodList = res.data
					this.isEmpty = false
				} else {
					this.prodList = []
					this.isEmpty = true
					this.guessLove()
				}
			})
		}
	}
}
